import React from 'react';
import { Grid } from '@material-ui/core';

import './App.css';
import { SearchBar, VideoDetail, VideoList } from './components';
import youtube from './api/youtube';
import RingLoader from 'react-spinners/RingLoader'

class App extends React.Component {
  state = {
    videos: [],
    selectedVideo: null,
  }

  onVideoSelect = (video) => {
    this.setState({ selectedVideo: video });
  }

  handleSubmit = async (SearchTerm) => {
    const response = await youtube.get('search', {
      params: {
        part: 'snippet',
        maxResults: 40,
        key: 'AIzaSyB-Upuuz3LN1f_l7tYX5U9rsec4arNHuw8',
        q: SearchTerm,
      }
    });

    this.setState({ videos: response.data.items, selectedVideo: response.data.items[0] });
  }
  render() {
    const { loading, selectedVideo, videos } = this.state;
    return (
      <Grid justify='center' container spacing={10} >
        <Grid item xs={12} className='wrapper' >
          <Grid container spacing={10} justify='center'>
            <Grid item xs={8} className='wrapperSearchBar'>
              <SearchBar onFormSubmit={this.handleSubmit} className='SearchBar' />
            </Grid >
            {loading ? <RingLoader /> : null}

            <Grid item xs={6} style={{ marginBottom: '20px' }} className='wrapperVideoDetail'>
              <VideoDetail video={selectedVideo} className='VideoDetail' />
            </Grid>
            <Grid item xs={8} className='wrapperVideoList'>
              <VideoList videos={videos} onVideoSelect={this.onVideoSelect} className='VideoList' />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    )
  }
}

export default App;