import React from 'react';

import { Grid, Paper, Typography } from '@material-ui/core';

const VideoItem = ({ video, onVideoSelect }) => {
  return (
    <Grid item xs={9} style={{ alignItems: 'center', justifyContent: 'center' }}>
      <Paper elevation={6} style={{ alignItems: 'center', height: '180px', overflow: 'hidden' }} onClick={() => onVideoSelect(video)}>
        <img alt="Thumbnail" src={video.snippet.thumbnails.medium.url} style={{ width: '100%', height: '60%' }} />
        <Typography variant="subtitle1"><b>{video.snippet.title} </b></Typography>
      </Paper>
    </Grid>
  )
}

export default VideoItem;